:root {
  --titlebar_h: 6vh;
  --editorcolor: rgb(228, 228, 228);
}
#editor {
  width: 43.5%;
  height: 100%;
  overflow-y: scroll;
  border: none;
  border-radius: none;
  margin-right: 0;
  margin-left: 1.5%;
  resize: none;
  font-family: monospace;
  font-size: large;
  background-color: var(--editorcolor);
}
#editor:focus {
  outline: none;
}
#preview {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
  padding: 0 1.5vw 0 1.5vw;
  width: 55%;
  overflow-wrap: break-word;
  background-color: white;
}
#preview code {
  background-color: rgb(214, 214, 214);
  border-radius: 5px;
}
#preview > pre {
  background-color: rgb(214, 214, 214);
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: "Times New Roman", Times, serif;
  border-radius: 5px;
  padding: 1.5vw;
}
img {
  max-width: 100%;
  height: auto;
}
table,
th,
td {
  border: 2px solid black;
  border-collapse: collapse;
}
